<template>
  <div class="home-content">
    <div class="header-content">
      <page-title :title="pageName"></page-title>
      <switch-btn-list :btnList="switchList" @changeType="changeAllType"></switch-btn-list>
    </div>
    <div class="main-content">
      <ZbCard v-for="(item, index) in cardList" :key="index" :zb-obj="item" :order="index"></ZbCard>
    </div>
  </div>
</template>

<script>
import pageTitle from '../../components/common/pageTitle'
import SwitchBtnList from '../../components/common/switchBtnList'
import ZbCard from '../../components/common/zbCard'
import _ from 'lodash'
import {initNum, formatNumber, switchPercent} from '../../assets/js/core/weapon' // 引入排序方法
import apiService from '../../assets/js/api'
export default {
  name: '',
  data () {
    return {
      pageName: '总体概览',
      switchList: [{ name: '月' }, { name: '季' }, { name: '年累' }, { name: '年' }],
      cardListDataInit: [],
      /** 带环形图的是type2
       *  其他的是type 1
       */
      cardListData: [
        // 第一行
        {zbName: '销售额(万)', subLabel: '同比:', firstValue: '0', secondValue: '0', thirdValue: '', type: '1', needArrow: true, showThreeRow: false, needThreeValue: false},
        {zbName: '销售达成', subLabel: '目标(万):', firstValue: '0', secondValue: '0', thirdValue: '', type: '2', needArrow: true},
        {zbName: '销售同比', subLabel: '', firstValue: '0%', secondValue: '0', thirdValue: '', type: '3', needArrow: true},
        // 第二行
        {zbName: '回款额(万)', subLabel: '同比:', firstValue: '0', secondValue: '0', thirdValue: '', type: '1', needArrow: true, showThreeRow: true, needThreeValue: false},
        {zbName: '回款达成', subLabel: '目标(万):', firstValue: '0', secondValue: '0', thirdValue: '', type: '2', needArrow: true},
        {zbName: '回款同比', subLabel: '', firstValue: '0%', secondValue: '0', thirdValue: '', type: '3', needArrow: true},
        // 第三行
        {zbName: '毛利额(万)', subLabel: '同比:', firstValue: '0', secondValue: '0%', thirdValue: '', type: '1', needArrow: true, showThreeRow: true, needThreeValue: false},
        {zbName: '扣前毛利率', subLabel: '同期:', firstValue: '0%', secondValue: '0', thirdValue: '0%', type: '1', needArrow: true, showThreeRow: true, needThreeValue: true},
        {zbName: '扣后毛利率', subLabel: '同期:', firstValue: '0%', secondValue: '0', thirdValue: '0%', type: '1', needArrow: true, showThreeRow: true, needThreeValue: true},
        // 第四行
        {zbName: '库存金额(万)', subLabel: '同比:', firstValue: '0', secondValue: '0%', thirdValue: '', type: '1', needArrow: false, showThreeRow: true, needThreeValue: false},
        // {zbName: '无销售SKU数(个)', subLabel: '同期:', firstValue: '0', secondValue: '0', thirdValue: '', type: '4', needArrow: false, showThreeRow: false, needThreeValue: false},
        {zbName: '控制差异率', subLabel: '目标:', firstValue: '0%', secondValue: '0', thirdValue: '', type: '1', needArrow: false, showThreeRow: true, needThreeValue: false},
        {zbName: '周转天数(天)', subLabel: '环比:', firstValue: '0', secondValue: '0%', thirdValue: '', type: '1', needArrow: false, showThreeRow: true, needThreeValue: false}
      ],
      // 这个用于界面渲染
      cardList: [],
      typeIndex: 0
    }
  },
  computed: {
    dateStr () {
      return this.$store.state.dateStr
    },
    dateStrM () {
      return this.$store.getters.dateStrM
    },
    dateStrJ () {
      return this.$store.getters.dateStrJ
    },
    selectedDpName () {
      return this.$store.state.selectedDpName
    },
    userIdentityInit () {
      return this.$store.state.userIdentityInit
    },
    selectedZgsName () {
      const authList = this.$store.state.authList
      let str = ''
      if (this.userIdentityInit === '2') {
        for (let item of authList) {
          if (item.shop === this.selectedDpName) {
            str = item.role
            break
          }
        }
      } else if (this.userIdentityInit === '3') {
        for (let item of authList) {
          if (item.role === this.selectedDpName) {
            str = item.zgs
            break
          }
        }
      }
      return str
    },
    selectedMultiShops () {
      return this.$store.state.selectedMultiShops
    }
  },
  methods: {
    changeAllType ({btn, index}) {
      this.typeIndex = index
      this.getCardListData(this.selectedZgsName, this.selectedDpName)
    },
    async getCardListData (companyName, dpName) {
      let url = ''
      if (this.typeIndex === 0) {
        url = apiService.homeShop.getCardListDataDay
      } else {
        url = apiService.homeShop.getCardListDataMon
      }
      const dpNameArr = this.$store.state.selectedMultiShops.map(item => {
        return item.name
      })
      const dpNameStr = dpNameArr.join(',')
      const {__statusCode, data} = await apiService.getDataFromInterface(url,
        {dateStrM: this.dateStrM, dateStrJ: this.dateStrJ, subName: companyName, cusName: dpNameStr})
      if (__statusCode === '1') {
        const result = JSON.parse(data)
        this.cardListDataInit = result
        this.dealCardListData()
      } else {
        alert('获取总览指标数据失败~')
      }
    },
    dealCardListData () {
      let dataList = []
      if (this.typeIndex === 0) {
        dataList = this.cardListDataInit.resM || []
      } else if (this.typeIndex === 1) {
        dataList = this.cardListDataInit.resJ || []
      } else if (this.typeIndex === 2) {
        dataList = this.cardListDataInit.resNl || []
      } else if (this.typeIndex === 3) {
        dataList = this.cardListDataInit.resY || []
      }
      const cardListTemp = _.cloneDeep(this.cardListData)
      if (dataList.length > 0) {
        const item = dataList[0]
        cardListTemp[0].firstValue = formatNumber(initNum(item.output_amt), 2) // 销售
        cardListTemp[0].secondValue = '' //
        cardListTemp[1].firstValue = switchPercent(item.yield_rate, 0) // 扣前销售 直营达成
        cardListTemp[1].secondValue = formatNumber(initNum(item.target_amt), 2) //  扣前销售 直营达成 目标
        cardListTemp[2].firstValue = switchPercent(item.output_amt_yoy, 0) // 扣前销售 同比
        cardListTemp[2].secondValue = '' //

        cardListTemp[3].firstValue = formatNumber(initNum(item.re_amt), 2) // 回款
        cardListTemp[3].secondValue = switchPercent(item.re_amt_yoy, 0) // 回款同比
        cardListTemp[4].firstValue = switchPercent(item.re_yield_rate, 0) // 回款达成
        cardListTemp[4].secondValue = formatNumber(initNum(item.re_target), 2) //  回款目标
        cardListTemp[5].firstValue = switchPercent(item.re_amt_yoy, 0) // 扣前销售 同比
        cardListTemp[5].secondValue = '' //

        cardListTemp[6].firstValue = formatNumber(initNum(item.margin_amt), 2) // 代理价毛利额
        cardListTemp[6].secondValue = switchPercent(item.margin_amt_yoy, 1, '%') // 代理价毛利额 同比
        cardListTemp[7].firstValue = switchPercent(item.margin_rate, 1, '%') // 代理价毛利额 扣前毛利率
        cardListTemp[7].secondValue = switchPercent(item.margin_rate_yoy, 1, '%') //  代理价毛利额 扣前毛利率 同期
        cardListTemp[7].thirdValue = switchPercent(item.margin_deviation, 1, '%') //  代理价毛利额 扣前毛利率 增减
        if (this.typeIndex === 0) {
          cardListTemp[8].firstValue = switchPercent(item.margin_deviation, 1, '%') // 代理价毛利额 扣后毛利率
          cardListTemp[8].secondValue = '' // 代理价毛利额 扣后毛利率 同期
          cardListTemp[8].thirdValue = '' // 代理价毛利额 扣后毛利率 增减
          cardListTemp[8].zbName = '毛利率差异'
          cardListTemp[8].showThreeRow = false
          cardListTemp[8].needArrow = true
        } else {
          cardListTemp[8].firstValue = switchPercent(item.margin_ded_rate, 0, '%') // 代理价毛利额 扣后毛利率
          cardListTemp[8].secondValue = switchPercent(initNum(item.margin_ded_rate_yoy), 1, '%') // 代理价毛利额 扣后毛利率 同期
          cardListTemp[8].thirdValue = switchPercent(initNum(item.margin_ded_deviation), 1, '%') // 代理价毛利额 扣后毛利率 增减
          cardListTemp[8].zbName = '扣后毛利率'
          cardListTemp[8].showThreeRow = true
          cardListTemp[8].needArrow = true
          cardListTemp[8].needThreeValue = true
        }
        cardListTemp[9].firstValue = formatNumber(initNum(item.inv_amt), 2) // 库存金额
        cardListTemp[9].secondValue = switchPercent(item.inv_yoy, 1, '%') // 库存同比
        cardListTemp[10].firstValue = switchPercent(item.diff_rate, 1, '%') // 控制差异率
        cardListTemp[10].secondValue = formatNumber(initNum(item.inv_target_amt), 2) // 控制差异率 目标
        cardListTemp[11].firstValue = formatNumber(initNum(item.zz_rate), 2) // 周转天数
        cardListTemp[11].secondValue = switchPercent(item.zz_rate_ring, 1, '%') // 周转天数环比
      }
      this.cardList = cardListTemp
    },
    getAllData () {
      this.getCardListData(this.selectedZgsName, this.selectedDpName)
    }
  },
  created () {
    this.$store.commit('updateHeaderTitle', '总览')
    this.$store.commit('updateShowIcon', true)
    this.$store.commit('updateDateStr', this.$store.state.dateStrInit)
    this.$store.dispatch('updateUserOption', {USER_ACCOUNT: window.userAccount, EXAMINATION_SUBJECT: '直营', LEVEL: '店铺', URL: '总览'})
    if (this.dateStr !== '') {
      this.getCardListData(this.selectedZgsName, this.selectedDpName)
    }
  },
  mounted () {
    this.$eventBus.$on('userSelected', this.getAllData)
  },
  beforeDestroy () {
    this.$eventBus.$off('userSelected', this.getAllData)
  },
  // watch: {
  //   dateStr: {
  //     handler () {
  //       this.getCardListData()
  //     }
  //   }
  // },
  components: {
    pageTitle,
    SwitchBtnList,
    ZbCard
  }
}
</script>

<style lang="scss" scoped>
  .home-content {
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    .header-content {
      width: 100%;
      height: px2rem(208);
      min-height: px2rem(208);
    }
    .main-content {
      flex: 1;
      width: 100%;
      overflow-y: auto;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-flow:row wrap;
    }

  }

</style>
